import Vue from 'vue'
import App from './App'

import router from './router'
// storemain.jsvue
import store from './store/index'
// 
// npmfont-awesome
// import 'font-awesome/css/font-awesome.css'
//axios
import axios from 'axios'

//axiosVuethis
Vue.prototype.axios = axios
//axios,windowthis
// window.aioxs=axios

Vue.config.productionTip = false;
//#appVue
window.bus = new Vue();

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store:store,
  template: '<App/>',
  components: { App }
})




